<template>
    <div class="v-emotion" v-loading="loading">
        <!---->
        <div class="m-emotion-single-container" v-if="id">
            <div class="m-emotion-goback">
                <el-button class="u-back" size="mini" icon="el-icon-arrow-left" @click="goBack"></el-button>
            </div>
            <emotion-item :emotion="emotion" mode="single"></emotion-item>
            <Thx
                class="m-thx"
                :postId="id"
                postType="emotion"
                :userId="user_id"
                :adminBoxcoinEnable="true"
                :userBoxcoinEnable="true"
            />
            <div class="m-single-comment">
                <el-divider content-position="left"></el-divider>
                <Comment :id="id" category="emotion" />
            </div>
        </div>

        <!---->
        <div class="m-emotion-list-container" v-else>
            <!--  -->
            <div class="m-emotion-search" slot="search-before">
                <el-input placeholder="" v-model="search">
                    <span slot="prepend"></span>
                    <template slot="append">
                        <el-switch
                            class="u-star"
                            v-model="star"
                            :inactive-value="0"
                            :active-value="1"
                            inactive-text=""
                        ></el-switch>
                        <el-switch
                            class="u-original"
                            v-model="original"
                            :inactive-value="0"
                            :active-value="1"
                            inactive-text=""
                        ></el-switch>
                    </template>
                </el-input>
            </div>
            <!--  -->
            <div class="m-emotion-types">
                <el-tabs v-model="type">
                    <el-tab-pane name="all" label="">
                        <span slot="label">
                            <i class="u-icon el-icon-menu" style="vertical-align: 0;"></i>
                        </span>
                    </el-tab-pane>
                    <el-tab-pane v-for="(item,i) in schoolmap" :key="i" :name="i">
                        <div slot="label" style="min-width:57px;">
                            <img class="u-icon" :src="i | showSchoolIcon" :alt="item" />
                            {{item}}
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </div>

            <!---->
            <emotion-post></emotion-post>

            <ul class="m-emotion-list" v-if="list && list.length">
                <waterfall
                    :autoResize="waterfall_options.autoResize"
                    :moveTransitionDuration="0.4"
                    :fillBox="waterfall_options.fillBox"
                    :list="list"
                    imgKey="src"
                    ref="waterfall"
                    :col-width="waterfall_options.colWidth"
                    :col="waterfall_options.col"
                >
                    <div
                        class="u-item waterfall-item"
                        :class="{fadeIn:item.state == 'show'}"
                        slot-scope="item"
                    >
                        <emotion-item
                            :emotion="item.data"
                            :index="item.index"
                            @preview="handlePreview"
                            :key="'emotion-' + item.data.type + '-' + item.data.id "
                        ></emotion-item>
                    </div>
                </waterfall>
            </ul>
            <!--  -->
            <el-alert v-else title="" type="info" show-icon></el-alert>
            <el-button
                style="width: 100%;"
                type="primary"
                @click="loadMore"
                v-show="page < pages"
                icon="el-icon-arrow-down"
                :disabled="loading"
            ></el-button>
            <!--  -->
            <!-- <el-pagination
                class="m-emotion-pagination"
                background
                :page-size="per"
                :hide-on-single-page="true"
                :current-page.sync="page"
                layout="total, prev, pager, next, jumper"
                :total="total"
                @current-change="skipTop"
            ></el-pagination>-->
        </div>
    </div>
</template>

<script>
import debounce from "lodash/debounce";
import waterfall from "vue-waterfall-rapid";

// 
import emotion_item from "@/components/emotion/emotion_item";
import emotion_post from "@/components/emotion/emotion_post";
import Comment from "@jx3box/jx3box-comment-ui/src/Comment.vue";
import {resolveImagePath} from '@jx3box/jx3box-common/js/utils'

// 
import schoolmap from "@jx3box/jx3box-data/data/xf/schoolid.json";
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";

// 
import { getEmotions, getEmotion } from "@/service/emotion";
import { getLikes } from "@/service/next";

export default {
    name: "Emotion",
    components: {
        "emotion-post": emotion_post,
        "emotion-item": emotion_item,
        Comment,
        waterfall,
    },
    data: function () {
        return {
            loading: false,

            // types
            schoolmap,

            // pagination
            type: "all",
            star: 0,
            original: 0,
            search: "",
            per: 50,
            page: 1,
            pages: 1,
            total: 0,
            emotions: [], //
            list: [], //
            appendMode: false,

            emotion: "",

            // 
            waterfall_options: {
                //
                autoResize: true,
                //
                fillBox: false,
                //-
                colWidth: 260,
                //
                col: 5,
            },
        };
    },
    computed: {
        id: function () {
            return ~~this.$route.params.id;
        },
        params: function ({ search, per, page, star, original }) {
            return {
                per,
                page,
                type: this.type == "all" ? "" : this.type,
                search,
                star,
                original,
            };
        },
        keys: function () {
            return [
                this.id,

                this.search,
                this.type,
                this.star,
                this.original,

                this.page,
                this.per,
            ];
        },
        reset_keys: function () {
            return [this.search, this.type, this.star, this.original];
        },
        user_id: function () {
            return this.emotion?.user_id || 0;
        },
        images: function () {
            return this.list.map((item) => resolveImagePath(item.url));
        },
        // new_pics: function () {
        //     return this.emotions.map((item) => {
        //         item.url;
        //     });
        // },
    },
    filters: {
        showSchoolIcon: function (val) {
            return __imgPath + "image/school/" + val + ".png";
        },
    },
    methods: {
        loadList: function () {
            this.loading = true;
            return getEmotions(this.params)
                .then((res) => {
                    if (this.appendMode) {
                        this.list = this.list.concat(
                            res.data?.data?.list || []
                        );
                        this.emotions = res.data?.data?.list || [];
                    } else {
                        this.list = this.emotions = res.data?.data?.list || [];
                    }
                    this.total = res.data.data.total;
                    this.pages = res.data.data.pages;

                    this.loadLike();
                    this.$nextTick(() => {
                        this.$refs.waterfall.repaints(this.page * this.per, 1);
                    });
                })
                .then(() => {
                    this.loading = true;

                    // let result = this.$refs.waterfall.repaints()
                    this.$refs.waterfall.onRender = (res) => {
                        this.loading = false;
                        console.log("waterfall", res);
                    };
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        loadSingle() {
            this.loading = true;
            getEmotion(this.id)
                .then((res) => {
                    this.emotion = res?.data?.data;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        loadMore: function () {
            this.appendMode = true;
            this.page++;
        },
        // 
        loadLike: function () {
            if (this.emotions && this.emotions.length) {
                let id = this.emotions.map((d) => "emotion-" + d.id);
                id = id.join(",");
                const params = {
                    post_type: "emotion",
                    post_action: "likes",
                    id: id,
                };
                getLikes(params).then((res) => {
                    const likes = res.data.data;
                    if (Object.keys(likes).length) {
                        this.emotions.forEach((d) => {
                            this.$set(
                                d,
                                "count",
                                likes?.["emotion-" + d.id]?.likes
                            );
                        });
                    }
                });
            }
        },

        // 
        goBack: function () {
            this.$router.push("/emotion");
        },
        skipTop: function () {
            window.scrollTo(0, 0);
        },

        // 
        calcCol: function () {
            let w = window.innerWidth;
            let col = 0;
            if (w < 780) {
                col = 2;
            } else if (w > 1024) {
                col = parseInt((window.innerWidth - 330) / 260); //
            } else {
                col = parseInt(window.innerWidth / 260); //
            }
            return col;
        },
        // 
        resizeCalc: function () {
            const vm = this;
            let repaint = debounce(function () {
                vm.waterfall_options.col = vm.calcCol();
            }, 200);
            window.addEventListener("resize", repaint);
        },
        // 
        handlePreview: function (i) {
            this.$hevueImgPreview({
                multiple: true, // 
                nowImgIndex: i, // 
                imgList: this.images, // 
                controlBar: false,
                clickMaskCLose: true,
            });
        },

        // 
        init: function () {
            if(this.id){
                this.loadSingle()
            }else{
                this.waterfall_options.col = this.calcCol();
                this.loadList();
            }
        },
    },
    watch: {
        keys: {
            deep: true,
            handler: function () {
                this.init();
            },
        },
        // 
        reset_keys: {
            deep: true,
            handler: function () {
                this.page = 1;
            },
        },
        // 
        search: function () {
            this.type = "all";
        },
        // emotions: {
        //     deep: true,
        //     handler() {
        //         this.loadLike();
        //     },
        // },
    },
    mounted: function () {
        this.init();
    },
    created: function () {
        this.resizeCalc()
    },
    filters: {
        showSchoolIcon: function (val) {
            return __imgPath + "image/school/" + val + ".png";
        },
    },
};
</script>

<style lang="less">
@import "~@/assets/css/emotion.less";
</style>
